@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

:root {
  --primary-color: #64ffda;
  --secondary-color: #0a192f;
  --background-color: #0a192f;
  --surface-color: #112240;
  --text-primary: #ccd6f6;
  --text-secondary: #8892b0;
  --accent-color: #64ffda;
  --transition-speed: 0.3s;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Space Grotesk', sans-serif;
  margin: 0;
  padding: 0;
  background: var(--background-color);
  color: var(--text-primary);
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.contact-info {
  position: fixed;
  top: 0;
  width: 100%;
  background: rgba(10, 25, 47, 0.85);
  padding: 1rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  z-index: 1000;
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(100, 255, 218, 0.1);
}

.contact-info a {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: all var(--transition-speed) ease;
  border: 1px solid transparent;
}

.contact-info a:hover {
  color: var(--accent-color);
  border-color: var(--accent-color);
  transform: translateY(-2px);
}

.container {
  height: 100vh;
  width: 100%;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
}

.section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  scroll-snap-align: start;
  position: relative;
  padding: 4rem 2rem;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

.section.animate {
  opacity: 1;
  transform: translateY(0);
}

.section-content {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
  background: var(--surface-color);
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(100, 255, 218, 0.1);
}

.section h1 {
  color: var(--accent-color);
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
}

.section p {
  color: var(--text-secondary);
  font-size: 1.1rem;
  line-height: 1.6;
  max-width: 800px;
  margin: 0 auto;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 1.5rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.box {
  background: var(--surface-color);
  padding: 1.5rem;
  border-radius: 12px;
  cursor: pointer;
  transition: all var(--transition-speed) ease;
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(100, 255, 218, 0.1);
}

.box:hover {
  transform: translateY(-5px);
  border-color: var(--accent-color);
}

.box h2 {
  color: var(--text-primary);
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
}

.box h3 {
  color: var(--accent-color);
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 1rem;
}

.box p {
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.6;
}

.box.expanded {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80vw;
  max-width: 1000px;
  height: auto;
  max-height: 80vh;
  overflow-y: auto;
  z-index: 1001;
  padding: 2rem;
  background: var(--surface-color);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.box.expanded h2 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}

.box.expanded h3 {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}

.box.expanded p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.section-navigation {
  position: fixed;
  right: 2rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  z-index: 1000;
}

.nav-button {
  background: transparent;
  border: none;
  color: var(--text-secondary);
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: all var(--transition-speed) ease;
  border-radius: 4px;
  opacity: 0.7;
}

.nav-button:hover {
  color: var(--accent-color);
  opacity: 1;
  transform: translateX(-5px);
}

.nav-button.active {
  color: var(--accent-color);
  opacity: 1;
  border: 1px solid var(--accent-color);
  transform: translateX(-5px);
}

.github-link {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background: transparent;
  color: var(--accent-color);
  text-decoration: none;
  border-radius: 4px;
  border: 1px solid var(--accent-color);
  transition: all var(--transition-speed) ease;
}

.github-link:hover {
  background: rgba(100, 255, 218, 0.1);
  transform: translateY(-2px);
}

.name-title {
  font-size: 4rem;
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-weight: 700;
}

.subtitle {
  color: var(--accent-color);
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.old-site-link {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  padding: 0.5rem 1rem;
  background: transparent;
  color: var(--accent-color);
  text-decoration: none;
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  transition: all var(--transition-speed) ease;
  z-index: 1000;
}

.old-site-link:hover {
  background: rgba(100, 255, 218, 0.1);
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .section-navigation {
    display: none;
  }
  
  .section-content {
    padding: 1.5rem;
  }
  
  .name-title {
    font-size: 2.5rem;
  }
  
  .subtitle {
    font-size: 1.2rem;
  }
  
  .grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .contact-info {
    gap: 1rem;
    padding: 0.5rem;
  }
  
  .contact-info a {
    font-size: 0.8rem;
    padding: 0.3rem 0.6rem;
  }
  
  .old-site-link {
    bottom: 1rem;
    right: 1rem;
    font-size: 0.8rem;
  }
}

/* Add smooth scroll behavior */
html {
  scroll-behavior: smooth;
}

/* Add a subtle gradient background */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.1) 0%, rgba(30, 64, 175, 0.1) 100%);
  z-index: -1;
}
